import { FiSearch } from "react-icons/fi";
import { FaArrowRight } from "react-icons/fa";

export default function CariBarang({ onBack }: { onBack: () => void }) {
  return (
    <div
      className="min-h-screen flex flex-col items-center justify-center bg-cover bg-center px-4 relative"
      style={{ backgroundImage: "url('/background.jpg')" }}
    >
      <button
        onClick={onBack}
        className="absolute top-4 right-4 bg-white text-black px-4 py-2 rounded-md text-sm font-medium shadow hover:bg-gray-100 transition"
      >
        Kembali
      </button>

      <h1 className="text-3xl md:text-4xl font-bold text-white mb-8">
        Cari Lokasi Produkt
      </h1>

      <div className="flex w-full max-w-xl items-center bg-white rounded-full shadow-lg overflow-hidden">
        <span className="pl-4 text-gray-400 text-xl">
          <FiSearch />
        </span>
        <input
          type="text"
          placeholder="Cari kode, nama dan model pakaian disini"
          className="flex-grow px-4 py-3 text-gray-800 bg-transparent focus:outline-none"
        />
        <button className="bg-black text-white px-5 py-3 rounded-r-full hover:bg-gray-800">
          <FaArrowRight />
        </button>
      </div>
    </div>
  );
}
