export default function EditBarang({ onBack }: { onBack: () => void }) {
  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gray-100 relative">
      <button
        onClick={onBack}
        className="absolute top-4 right-4 bg-white text-black px-4 py-2 rounded-md shadow hover:bg-gray-200"
      >
        Kembali
      </button>

      <h1 className="text-3xl font-bold mb-6">Edit Barang</h1>

      <form className="bg-white shadow-lg rounded p-6 w-full max-w-md space-y-4">
        <input
          type="text"
          placeholder="Kode Barang"
          className="w-full border px-4 py-2 rounded"
        />
        <input
          type="text"
          placeholder="Nama Baru"
          className="w-full border px-4 py-2 rounded"
        />
        <input
          type="number"
          placeholder="Stok Baru"
          className="w-full border px-4 py-2 rounded"
        />
        <button
          type="submit"
          className="w-full bg-black text-white py-2 rounded hover:bg-gray-800"
        >
          Simpan Perubahan
        </button>
      </form>
    </div>
  );
}
