export default function HapusBarang({ onBack }: { onBack: () => void }) {
  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-red-50 relative">
      <button
        onClick={onBack}
        className="absolute top-4 right-4 bg-white text-black px-4 py-2 rounded-md shadow hover:bg-gray-200"
      >
        Kembali
      </button>

      <h1 className="text-3xl font-bold mb-6 text-red-700">Hapus Barang</h1>

      <form className="bg-white shadow-lg rounded p-6 w-full max-w-md space-y-4">
        <input
          type="text"
          placeholder="Kode Barang"
          className="w-full border px-4 py-2 rounded"
        />
        <button
          type="submit"
          className="w-full bg-red-600 text-white py-2 rounded hover:bg-red-700"
        >
          Hapus
        </button>
      </form>
    </div>
  );
}
